<template>
  <div class="wyl_big">
    <!-- header -->
    <header>
      <!---->
      <h3>特色课</h3>
      <div class="right">
        <img
          src="data:image/png;base64,"
          alt
          class="search"
          @click="back"
        />
      </div>
    </header>




    <!--wyl_nav -->
    <van-dropdown-menu class="wyl_nav" active-color="#EE8032">
      <van-dropdown-item title="分类">
        <!--option one -->
        <div class="nkl_option1">
          <div>
            <p>年级</p>
            <div>
              <span v-for="(item,index) in option1.nianji" :key="index">{{ item }}</span>
            </div>
          </div>
          <div>
            <p>课程</p>
            <div>
              <span v-for="(item,index) in option1.xueke" :key="index">{{ item }}</span>
            </div>
          </div>
          <div>
            <button>重置</button>
            <button>确定</button>
          </div>
        </div>
      </van-dropdown-item>
      <!-- option two -->
      <van-dropdown-item title="排序">
        <div class="nkl_option2">
          <p v-for="(item,index) in option2" :key="index">{{item}}</p>
        </div>
      </van-dropdown-item>
      <!-- option three -->
      <van-dropdown-item title="筛选">
        <div class="nkl_option3">
          <span v-for="(item,index) in option3" :key="index">{{item}}</span>
        </div>
      </van-dropdown-item>
    </van-dropdown-menu>








    <!-- main -->

      <div class="list">
              <div class="context">
                <!-- item one -->
                <div class="item">
                  <div class="ii-item" v-for="(item,index) in MainCourseList" :key="index">
                    <p class="ii-title">
                      <font>{{item.title}}</font>
                    </p>
                    <div class="ii-time">
                      <p class="after-icon">03月16日 18:30 ~ 03月22日 15:00</p>
                      <p>共{{item.total_periods}}课时</p>
                    </div>
                    <div class="ii-teacher">
                      <div class="teacher-item" v-for="(v,i) in item.teachers_list" :key="i">
                        <img class="zjy_img" :src="v.teacher_avatar" />
                        <font>{{ v.teacher_name }}</font>
                      </div>
                    </div>
                    <p class="ii-info">
                      <span class="person">{{item.sales_num}}人已报名</span>
                      <font class="free" v-if="item.price==0">{{item.price | dd}}</font>
                      <font class="price" v-else>
                        <img src="../../assets/pic/gold.png" alt="">
                        {{item.price | dd}}</font>
                    </p>
                  </div>
                </div>
              </div>
            </div>
    <!-- 底部导航 -->
    
<zzFooter></zzFooter>
<!-- <wylIcon></wylIcon> -->
  </div>
</template>

<script>
// 引入小icon留言
// import wylIcon from "../dragball"
// 引入底部导航
import  zzFooter from '../footbar'

export default {
  name: "",
  data() {
    return {
      option1: {
        nianji: ["初一", "初二", "初三", "高一", "高二"],
        xueke: ["语文", "数学", "英语", "物理", "化学"]
      },
      option2: ["综合排序", "最新", "最热", "价格从低到高", "价格从高到低"],
      option3: [
        "全部",
        "大班课",
        "小班课",
        "公开课",
        "点播课",
        "面授课",
        "音频课",
        "系统课",
        "图文课",
        "会员课"
      ],
      page: 1,
      list: [],
      loading: false,
      MainCourseList:[],
      MainCourseList1:[],
    };
  },
  props: {},
  components: {
    zzFooter,
    // wylIcon,
  },
  filters:{
     // 免费 过滤
    dd(a) {
      if (a == 0) {
        return "免费";
      } else {
        return "￥" + Number(a).toFixed(2);
      }

      return a;
    }
  },
  mounted() {
    this.ajaxMainCourse()
  },
  methods: {
    back(){
      this.$router.push('/zjySearch')
    },
    // 用的是主讲课程
    async ajaxMainCourse() {
      let {
        data: res
      } = await this.$http.post(
        `https://www.365msmk.com/api/app/teacher/mainCourse`,
        { limit: 10, page: 1, teacher_id: this.$route.query.teacher_id }
      );
      console.log(res.data);
      this.MainCourseList = res.data.list;
      console.log(this.MainCourseList);
      this.MainCourseList1 = res.data.list.teachers_list;
    },
  }
};
</script>

