<script>
export default {
  data() {
    return {
      
      xq: [],
      value:5,
    };
  },
  created() {
    console.log(this.$route.query.id);
    // var id = this.$route.query.id;
    this.$axios
      .get(
        `https://www.365msmk.com/api/app/courseInfo/basis_id=+${this.$route.query.id}`
      )
      .then(res => {
        console.log(res.data.data);
        // this.xq.push(res.data.data);
        // console.log(this.xq[0])
        this.xq = res.data.data;
      });
  },
  mounted() {},
  filters: {
    filterprice(val) {
      if (val == 0) {
        return "免费";
      }
      return val;
    },
    periods(val) {
      return "共" + val + "课时 |";
    },
    num(val) {
      return val + "人已报名";
    }
  },
  methods: {
    goleft() {
      this.$router.go(-1);
    },
    zjysc(){
        console.log(this.$route.query.id)
        this.$axios
      .post("https://www.365msmk.com/api/app/collect",{course_basis_id:this.$route.query.id,type:1})
      .then(res => {
        // console.log( course_basis_id)
        console.log(res);
        // this.xq.push(res.data.data);
        // console.log(this.xq[0])
        this.xq = res.data.data;
      });
    }
  }
};
