<template>
  <div class="index">
    <!-- swiper -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(item,index) of banner" :key="index">
        <img :src="item.banner_img" alt />
      </van-swipe-item>
    </van-swipe>

    <!-- nav -->
    <ul class="nav">
      <li v-jump="path1">
        <img src="../assets/pic/characteristic.png" alt />
        <font>特色课</font>
      </li>
      <li v-jump="path2">
        <img src="../assets/pic/coach.png" alt />
        <font>一对一辅导</font>
      </li>
      <li v-jump="path3">
        <img src="../assets/pic/Study-Calendar.png" alt />
        <font>学习日历</font>
      </li>
    </ul>

    <!-- 四个页面 -->

    <section v-for="(item,index) of indexList" :key="index">
      <!-- 名师阵容 -->
      <div v-if="item.channel_info.type == 3">
        <p class="op-title">{{ item.channel_info.name }}</p>
        <ul class="oto-content">
          <li class="oc-item" v-for="(v,i) of item.list" :key="i" @click="toggle1(v,i)">
            <img :src="v.teacher_avatar" alt />
            <div>
              <p>{{ v.teacher_name }}</p>
              <p>{{ v.introduction }}</p>
            </div>
          </li>
        </ul>
      </div>
      <!-- 精品课堂 -->
      <div v-if="item.channel_info.type == 1" >
        <p class="op-title">{{ item.channel_info.name }}</p>
        <ul class="index-item-page">
          <li class="ii-item" v-for="(v,i) of item.list" :key="i" @click="gocoursexq(v.id)">
            <p class="ii-title">{{ v.title }}</p>
            <p class="ii-time">共{{ v.total_periods }}课时</p>
            <div class="ii-teacher">
              <img :src="v.teachers_list[0].teacher_avatar" alt />
              <font>{{ v.teachers_list[0].teacher_name }}</font>
            </div>
            <p class="ii-info">
              <span class="person">{{ v.sales_num }}人已报名</span>
              <span class="free" v-if="v.price ==0">免费</span>
              <span class="price" v-else>
                <img src="../assets/pic/gold.png" alt />
                {{ v.price | much }}
              </span>
            </p>
          </li>
        </ul>
      </div>
      
      <!-- 明星讲师 -->
      <div v-if="item.channel_info.type == 4">
        <p class="op-title">{{ item.channel_info.name }}</p>
        <ul class="oto-content">
          <li class="oc-item" v-for="(v,i) of item.list" :key="i" @click="toggle1(v,i)">
            <img :src="v.teacher_avatar" alt />
            <div>
              <p>
                <span>{{ v.teacher_name }}</span>
                <font class="zjyfont">{{ v.level_name }}</font>
              </p>
              <p>{{ v.introduction }}</p>
            </div>
          </li>
        </ul>
      </div>
    </section>
    <!-- footer -->
    <zzFooter></zzFooter>
    <!-- <wylIcon></wylIcon> -->
  </div>
</template>

<script>
// 引入小icon留言
// import wylIcon from "../components/dragball"
// 引入底部导航
import zzFooter from "../components/footbar";
export default {
  name: "",
  data() {
    return {
      banner: [],
      indexList: [],

      //上面 三个（nav） 定位  的 页面跳转   使用时的  v-jump
      path1: "/WylCC", //特色课  wyl
      path2: "/zjy", //一对一辅导  zjy
      path3: "/rili", // 日历   zjy
      // 四个模块
      path4: "/xiangqing", //名师详情  zjy
      // path5: "/WylCD" //课程详情  wyl
    };
  },
  props: {},
  created() {
    this.ajaxBannerList();
    this.ajaxIndexList();
  },
  mounted() {},
  methods: {
    async ajaxBannerList() {
      let { data: res } = await this.$http.get(
        "https://www.365msmk.com/api/app/banner"
      );
      this.banner = res.data;
    },
    async ajaxIndexList() {
      let { data: res } = await this.$http.get(
        "https://www.365msmk.com/api/app/recommend/appIndex"
      );
      console.log(res.data);
      this.indexList = res.data;
    },

    // 名师阵容  跳转到  讲师详情
    toggle1(i, a) {
      // console.log(i[a])
      this.$router.push({
        path: "/xiangqing", //zjy
        query: {
          teacher_id: i.teacher_id
        }
      });
    },
      gocoursexq(id){
      console.log('输出id',id)
      this.$router.push({
        path:"/CourseXq",
        query:{id:id}
      })
    }
  },
  computed: {},
  watch: {},
  filters: {
    much(val) {
      return (val / 100).toFixed(2);
    }
  },
  directives: {},
  components: {
    zzFooter
    // wylIcon,
  }
};
</script>
<style scoped lang="scss">
img {
  vertical-align: middle;
}
.index {
  background: #f0f2f5;
  height: 740%;
}
.my-swipe .van-swipe-item {
  height: 4.5rem;
  > img {
    width: 100%;
    height: 100%;
  }
}
.nav {
  width: 100%;
  display: flex;
  justify-content: space-around;
  position: relative;
  top: -0.5rem;
  > li {
    width: 25%;
    border-radius: 0.15rem;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
    height: 1.8rem;
    font-size: 0.3rem;
    flex-direction: column;
    background: #fff;
    text-align: center;
    > img {
      width: 0.55rem;
    }
    > font {
      font-size: 0.2rem;
      margin-top: 0.2rem;
      color: #8c8c8c;
    }
  }
}
p {
  margin: 0;
}
.op-title {
  margin-left: 0.35rem;
  border-left: 0.07rem solid #eb6100;
  font-size: 0.3rem;
  color: #595959;
  text-align: left;
  padding-left: 0.25rem;
  margin-bottom: 0.3rem;
}
.oto-content {
  padding: 0.1rem 0.2rem 0.5rem;
  .oc-item {
    height: 2.15rem;
    background: #fff;
    border-radius: 0.15rem;
    display: flex;
    align-items: center;
    padding: 0 0.4rem;
    margin-top: 0.2rem;
    > img {
      width: 1.02rem;
      height: 1.02rem;
      border-radius: 50%;
    }
    > div {
      flex: 1;
      margin-left: 0.2rem;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      p {
        display: block;
        text-align: left;
      }
      > p:nth-of-type(1) {
        font-size: 0.28rem;
        color: #000;
      }
      > p:nth-of-type(2) {
        font-size: 0.26rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }
  }
}
.index-item-page {
  padding: 0.4rem;
  .ii-item {
    background: #fff;
    border-radius: 0.15rem;
    padding: 0 0.35rem;
    margin-bottom: 0.4rem;
    // position: relative;
    .ii-title {
      padding-top: 0.4rem;
      text-align: left;
      font-size: 0.3rem;
      color: #333;
      font-weight: 400;
    }
    .ii-time {
      height: 0.8rem;
      line-height: 0.8rem;
      font-size: 0.28rem;
      color: #666;
      font-weight: 400;
      text-align: left;
    }
    .ii-teacher {
      height: 1.2rem;
      display: flex;
      align-items: center;
      > img {
        width: 0.72rem;
        height: 0.72rem;
        border-radius: 50%;
      }
      > font {
        font-size: 0.28rem;
        color: rgba(0, 0, 0, 0.45);
        margin-left: 0.2rem;
        font-weight: 400;
      }
    }
    .ii-info {
      border-top: 0.01rem solid #f5f5f5;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 1rem;
      .person {
        font-size: 0.28rem;
        color: rgba(0, 0, 0, 0.45);
      }
      .free {
        color: #44a426;
        font-size: 0.3rem;
      }
      .price {
        color: #ee1f1f;
        font-size: 0.3rem;
        > img {
          width: 0.32rem;
        }
      }
    }
  }
}
.zjyfont{
  color:#ED8C4C;
  margin-left: 0.1rem;
}
</style>