<template>
  <div id="app">
    <div class="zjy_d1">
      <div>
        <van-icon name="arrow-left" @click="onClickLeft" />
      </div>
      <div>讲师详情</div>
      <div></div>
    </div>

    <div v-if="Insto1">
      <!-- zjy3 in -->
      <div class="zjy_3">
        <div>
          <img class="zjy_img" :src="Insto1.avatar" />
        </div>
        <div>
          <p>{{ Insto1.teacher_name }}</p>
          <p class="zjy_fon">
            {{Insto1.sex | aa}}
            <span>{{Insto1.teach_age}}教龄</span>
          </p>
        </div>
        <div @click="collectit">{{Insto.flag | bb}}</div>
      </div>
    </div>
    <div class="zjy_w1" v-if="Instructor">
      <van-tabs color="#eb6100" line-width="0.2rem" title-active-color="#eb6100">
        <!--  -->
        <van-tab title="讲师介绍">
          <div class="zjy_box">
            <div class="zjy_left">
              <ul>
                <li v-for="(item,index) in noid(Instructor)" :key="index">{{ item.attr_name }}</li>
                <li>老师简介</li>
              </ul>
            </div>
            <div class="zjy_right">
              <ul>
                <li v-for="(item,index) in noid(Instructor)" :key="index">
                  <p v-for="(it,ind) in item.attr_value" :key="ind">{{it.attr_value_name}}</p>
                </li>
                <li>{{ instro }}</li>
              </ul>
            </div>
          </div>
        </van-tab>
        <!--  -->
        <van-tab title="主讲课程">
          <div class="wyl_box">
            <div class="wyl_list">
              <div class="context">
                <!-- item one -->
                <div class="item">
                  <div class="ii-item" v-for="(item,index) in MainCourseList" :key="index">
                    <p class="ii-title">
                      <font>{{item.title}}</font>
                    </p>
                    <div class="ii-time">
                      <p class="after-icon">03月16日 18:30 ~ 03月22日 15:00</p>
                      <p>共{{item.total_periods}}课时</p>
                    </div>
                    <div class="ii-teacher">
                      <div class="teacher-item" v-for="(v,i) in item.teachers_list" :key="i">
                        <img class="zjy_img" :src="v.teacher_avatar" />
                        <font>{{ v.teacher_name }}</font>
                      </div>
                    </div>
                    <p class="ii-info">
                      <span class="person">{{item.sales_num}}人已报名</span>
                      <font class="free" v-if="item.price==0">{{item.price | dd}}</font>
                      <font class="price" v-else>
                        <img src="../assets/pic/gold.png" alt />
                        {{item.price | dd}}
                      </font>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </van-tab>
        <van-tab title="学员评价">
          <!-- zz -->
          <!-- <div class="commentarea">
            <div v-if="comments" class="commentbox"></div>
             <div v-else class="nocmment">
              <dl class="alertbox">
                <dt class="alertimgbox">
                  <img src="../assets/img/none.png" alt="" class="alertimg">
                </dt>
                <dd class="alerttxt">暂无评论</dd>
              </dl>
            </div>
          </div>-->
          <div class="wyl_box">
            <img src="../assets/img/none.png" alt />
          </div>
        </van-tab>
      </van-tabs>
    </div>

    <div class="zjy_yy">立即预约</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      Instructor: null,
      instro: "",
      Insto: null,
      Insto1: null,
      MainCourseList: []
    };
  },
  created() {
    // console.log(this.$route.query.teacher_id)
  },
  mounted() {
    this.ajaxInstructorList();
    this.ajaxMainCourse();
    this.ajaxInstoList();
    this.getcollect()
  },
  computed: {
    noid() {
      return function(arr) {
        return arr.filter((m, i) => {
          return !m.id;
        });
      };
    }
  },
  filters: {
    // 男女
    aa(a) {
      if (a == 0) {
        return "男";
      } else {
        return "女";
      }
      return a;
    },
    // 关注。  已关注
    bb(flag) {
      if (flag == 0) {
        return "关注";
      } else {
        return "已关注";
      }
      return flag;
    },


    // 免费 过滤
    dd(a) {
      if (a == 0) {
        return "免费";
      } else {
        return "￥" + Number(a).toFixed(2);
      }

      return a;
    }
  },
  methods: {
    // 返回
    onClickLeft() {
      this.$router.go(-1);
    },
    // 接口调用
    // 讲师介绍
    async ajaxInstructorList() {
      let { data: res } = await this.$http.get(
        `https://www.365msmk.com/api/app/teacher/info/${this.$route.query.teacher_id}`
      );
      // console.log(res.data);
      this.Instructor = res.data.attr;
      this.instro = res.data.intro;
      // console.log(this.Instructor);
    },

    // 主讲课程
    async ajaxMainCourse() {
      let {
        data: res
      } = await this.$http.post(
        `https://www.365msmk.com/api/app/teacher/mainCourse`,
        { limit: 10, page: 1, teacher_id: this.$route.query.teacher_id }
      );
      // console.log(res.data);
      this.MainCourseList = res.data.list;
      // console.log(this.MainCourseList);
      this.MainCourseList1 = res.data.list.teachers_list;
    },
    // 上面介绍  老师的  关注  教龄
    async ajaxInstoList() {
      let { data: res } = await this.$http.get(
        `https://www.365msmk.com/api/app/teacher/${this.$route.query.teacher_id}？page=1&&limit=2`
      );
      // console.log(res.data);
      this.Insto1 = res.data.teacher;
      this.Insto = res.data;
      // console.log(this.Insto1);
    },
    collectit(){
      if(this.Insto.flag-1){
      this.$http.get(
        'https://www.365msmk.com/api/app/teacher/collect/'+this.$route.query.teacher_id,{
          
        }
      ).then(res=>{
        console.log(res.data)
      })
      }else{
      this.$http.post(
        `https://www.365msmk.com/api/app/collect/cancel/${this}/${this.$route.query.teacher_id}`,{

        }
      ).then(res=>{
        // console.log(res.data)
      })
      }
    },
    async getcollect(){
      console.log(123)
      let {data:res}=await this.$http.post(
        'https://www.365msmk.com/api/app/collect',{
          page:1,
          limit:10,
          type:2
        }
      )
      // console.log(res)
      
    }
  }
};
</script>

