
<style scoped lang='scss'>
.studyprocess {
  display: flex;
  height: 1rem;
  align-items: center;
  & > div:not(.probox) {
    flex: 1;
  }
  .probox {
    flex: 2;
  }
}
.detailbox {
    border: .53333vw solid #e9e9e9;
    margin: 4vw;
    padding: 4vw;
    &-title {
        .name {
            position: relative;
            padding-left: 4vw;
            text-align: left;
            &:before {
                content: "";
                display: block;
                width: .10667rem;
                width: 1.06667vw;
                height: .10667rem;
                height: 1.06667vw;
                border-radius: 50%;
                background-color: #eb6100;
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
            }
        }
    }
    &-teacher {
        .name {
            
        }
    }
    &-info {
        display: flex;
        align-items: center;
        &-blank {
            flex: 1;
        }
        &-stick {
            flex: 2;
        }
        &-txt {
            flex: 1;
            color: #8c8c8c;
        }
    }
}
</style>


<template>
  <div>
    <van-nav-bar
      :title="tit"
      right-text="目"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <div class="studyprocess">
      <div>共{{ term }}课时</div>
      <div class="probox">
        <van-progress color="#f2826a" :percentage="rate" :show-pivot="false" />
      </div>
      <div>已学习{{ rate }}%</div>
    </div>
    <div class="detailbox">
      <div class="detailbox-title">
        <span class="name">{{ tit }}</span>
      </div>
      <div class="detailbox-teacher">
        <span class="name"></span>
      </div>
      <div class="detailbox-info">
          <div class="detailbox-info-blank">
              
          </div>
        <van-progress color="#f2826a" class="detailbox-info-stick" :percentage="rate" :show-pivot="false" />
        <span class="detailbox-info-txt">已观看{{ rate }}%</span>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      tit: "课程",
      term: 0,
      rate: 36,
    };
  },
  created() {},
  mounted() {},
  //props:{},//[]
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.$router.push("/rili");
    }
  },
  computed: {},
  watch: {},
  filters: {},
  directives: {},
  components: {}
};
</script>